/*Here i will use variables, Inheritance and nesting*/

/*---------------My variables*---------------*/
$main-color: #E4E4E4;
$site-color: #273238;
/*---------------My variables*---------------*/

/*---------------My Inheritances-------------*/
%main-text {
  font-family: malgun gothic, sans-serif;
  font-size: 25px;
}

%center {
  display: flex;
  align-items: center;
  justify-content: center;
}
/*---------------My Inheritances-------------*/

body {
  background-color: $main-color;
  margin: 0px;
  padding: 0px;
}

nav {
  background-color: $site-color;
  width: 100%;
  height: 85px;
  @extend %center;
}

p {
  @extend %main-text;
  color: $site-color;
}

a {
  @extend %main-text;
  color: $main-color;
  text-decoration: none;
  width: 10%;
}

nav {
  img {
    height: 70px;     
  }
}
